body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}
.ui.menu .item img.logo {
  margin-right: 1.5em;
}

.main.container {
    margin-top: 5em;
}

ul.ui.list li:before {
  display: none;
  content: '';
}

.buttons-box, .miniform { display: block; margin: 50px auto; width: 500px; }

.btn { display: inline-block; width: 100px; padding: 10px; border-radius: 15px; background: #c3c3c3; color: #fff; }

output { display: block; padding: 10px 20px; font-weight: bold; font-size: 18px; }

label, input { display: block; margin: 20px 0}

